.spinner {
  position: absolute;
  top: 2px;
  left: 3px;
  width: 42px;
  height: 42px;
  border: 3px solid transparent;
  border-radius: 500px;
  border-top-color: var(--player-loader-color);
  -webkit-animation: circle-button-spinning 0.65s ease infinite;
          animation: circle-button-spinning 0.65s ease infinite;
}

@-webkit-keyframes circle-button-spinning {
  0% {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
  }

  to {
    -webkit-transform: rotate(315deg);
            transform: rotate(315deg);
  }
}

@keyframes circle-button-spinning {
  0% {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
  }

  to {
    -webkit-transform: rotate(315deg);
            transform: rotate(315deg);
  }
}
/* controls */
